@mixin if-is-tablet {
    @media all and (min-width: #{$tablet-width}) and (max-width: #{$desktop-width}){
        @content;
    }
}

@mixin if-is-phone {
     @media all and (max-width: #{$phone-width}){
         @content;
     }
}
@mixin if-is-desktop{
    @media all and (min-width: #{$desktop-width}){
        @content;
    }
}
