.container{
    width: 100%;
    //max-width: 1200px;
    margin: 0 auto; /*arriba y abajo 0, y por los lados me ponga margenes automaticos*/
}
.row{
    box-sizing: border-box;
}
/*Clear fix hack*/
.row :before, .row :after{
    content: ""; /*cambia el contenido del html, limpia*/
    display: table;
    clear: both;
}
.col-1{
    width: 8.33333333%;
    float: left; /* para que los elementos se acomoden de izquierda a derecha*/
    display: inline-block; /* para que lo muestre lineal */
}
